.gload-image-container{background-color:#e1f0ef;background-size:cover;background-repeat:no-repeat;position:relative;overflow:hidden}.gload-image-container canvas,.gload-image-container img{position:absolute;opacity:0;top:0;left:0;-webkit-transition:opacity 1s linear;transition:opacity 1s linear}.gload-image-container canvas.loaded,.gload-image-container img.loaded{opacity:1}